import { createPopper } from '@popperjs/core';
export function _newCreatePopper(reference, popper, placement, offset) {
    return createPopper(reference, popper, {
        placement: placement,
        modifiers: [
            {
                name: 'computeStyles',
                options: {
                    gpuAcceleration: false // top/left
                }
            },
            {
                name: 'computeStyles',
                options: {
                    adaptive: false // 
                }
            },
            {
                name: 'offset',
                options: {
                    offset: [offset] // 
                }
            }
        ]
    });
}
