import { bind } from '../dom-utils';
/**
 * 
 * @param elem
 * @param callback 
 * @param child
 * @param datasetKey
 * @param datasetVal 
 */
export default function clickOutside(elem, callback, child, datasetKey, datasetVal) {
    var ev = function (e) {
        if (datasetKey) {
            if (child) {
                // @ts-ignore
                child.dataset[datasetKey] === datasetVal && callback(e);
            }
            else {
                // @ts-ignore
                child.dataset[datasetKey] === datasetVal && callback(e);
            }
        }
        else {
            callback();
        }
    };
    bind(document, 'click', function (e) { return ev(e); });
    bind(elem, 'click', function (e) { return e.stopPropagation(); });
}
